$(function(){
$('.google-slider').slick({
    slidesToShow: 4,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2500,
    infinite: true,
    arrows: true,
    dots: true,
   
    responsive: [
        { breakpoint: 992, settings: { slidesToShow: 2 } },
        { breakpoint: 576, settings: { slidesToShow: 1 } }
    ]
});



});

// slider js end


// faq js start

$(function () {
    $(".faq__box-question").on("click", function () {
        const parentBox = $(this).parent(".faq__box");

        // Закрываем все открытые
        $(".faq__box").not(parentBox).removeClass("active");

        // Тогглим текущий
        parentBox.toggleClass("active");
    });
});



// faq js end



